import {
    Component,
    OnInit,
    OnDestroy,
    Input,
    Output,
    ViewChild,
    ElementRef,
    EventEmitter,
    HostBinding
} from "@angular/core";

import { FPhiSelphIdWidgetService } from "../../services/fphi-selphid-widget.service";
import { FPhiSelphIdConfiguration, FPhiSelphIdDocumentDimensions } from "../../models/facephi-selphid-configuration";

@Component({
    selector: "facephi-selphid",
    templateUrl: "./facephi-selphid.component.html",
    styleUrls: ["./facephi-selphid.component.scss"]
})
export class FacePhiSelphIdComponent implements OnInit, OnDestroy {

    // #region "Atributos"

    private _cssClass: string = "";
    private _widgetConfiguration: FPhiSelphIdConfiguration = new FPhiSelphIdConfiguration();

    // #endregion

    // #region "Propiedades"

    @Input("class")
    @HostBinding("class")
    public get cssClass(): string {
        return this._cssClass;
    }

    public set cssClass(value: string) {
        if (value)
            this._cssClass += ` ${value}`;
    }

    /**
     * Anchura del componente.
     */
    @Input("width")
    public componentWidth: string;

    public get widgetWidth(): string {
        return this.componentWidth
            ? typeof this.componentWidth === "string" && this.componentWidth.endsWith("%")
                ? this.componentWidth
                : `${parseFloat(this.componentWidth)}px`
            : "100%";
    }

    /**
     * Altura del componente.
     */
    @Input("height")
    public componentHeight: string;

    public get widgetHeight(): string {
        return this.componentHeight
            ? typeof this.componentWidth === "string" && this.componentHeight.endsWith("%")
                ? this.componentHeight
                : `${parseFloat(this.componentHeight)}px`
            : "100%";
    }

    /**
     * Referencia interna al contenedor sobre el que se generarÃ¡ el widget web.
     */
    @ViewChild("widgetContainer")
    public readonly widgetContainer: ElementRef;

    /**
     * Clases CSS aplicadas sobre el contenedor del widget web.
     */
    @Input()
    public containerCssClass: string;

    // #region "ConfiguraciÃ³n"

    /**
     * Ruta en la que estÃ¡n localizados los recursos javascript nativos del widget web.
     */
    @Input()
    public widgetLibPath = "assets/lib/facephi/selphid";

    /**
     * Flag para indicar si queremos que sea el componente el encargado de cargar e inicializar todos los recursos javascript del widget y borrarlos en su evento 'onDestroy'.
     */
    @Input()
    public autoLoadLibResources: boolean = true;

    /**
     * Idioma en que se mostrarÃ¡ la informaciÃ³n del widget.
     */
    @Input()
    public set language(value: string) {
        this._widgetConfiguration.language = value;
    }

    public get language(): string { return this._widgetConfiguration.language; }

    /**
     * Lista de densidades de pÃ­xeles en pantalla soportadas por el widget, de cara a mostrar las imÃ¡genes de recurso adecuadas a la pantalla actual.
     */
    @Input()
    public set dpiList(value: Array<number>) {
        this._widgetConfiguration.dpiList = value;
    }

    public get dpiList(): Array<number> { return this._widgetConfiguration.dpiList; }

    /**
     * Modo de captura de documento. Controla el flujo de ejecuciÃ³n del widget.
     */
    @Input()
    public set captureMode(value: number) {
        this._widgetConfiguration.captureMode = value;
    }

    public get captureMode(): number { return this._widgetConfiguration.captureMode; }

    /**
     * En caso de estar habilitado, el widget mostrarÃ¡ el documento capturado y solicitarÃ¡ confirmaciÃ³n al usuario.
     */
    @Input()
    public set previewCapture(value: boolean) {
        this._widgetConfiguration.previewCapture = value;
    }

    public get previewCapture(): boolean { return this._widgetConfiguration.previewCapture; }

    /**
     * Dimensiones fÃ­sicas (en mm) del documento a capturar.
     */
    @Input()
    public set documentDimensions(value: FPhiSelphIdDocumentDimensions) {
        this._widgetConfiguration.documentDimensions = value;
    }

    public get documentDimensions(): FPhiSelphIdDocumentDimensions { return this._widgetConfiguration.documentDimensions; }

    /**
     * Umbral de tolerancia para dar por vÃ¡lida una captura de documento.
     */
    @Input()
    public set qualityThreshold(value: number) {
        this._widgetConfiguration.qualityThreshold = value;
    }

    public get qualityThreshold(): number { return this._widgetConfiguration.qualityThreshold; }

    /**
     * Factor de escalado sobre el Ã¡rea de recorte de la imagen del documento. La imagen devuelta se escala de acuerdo con este valor.
     * Solamente se permiten valores mayores o iguales a 1.0.
     */
    @Input()
    public set cropFactor(value: number) {
        this._widgetConfiguration.cropFactor = value;
    }

    public get cropFactor(): number { return this._widgetConfiguration.cropFactor; }

    /**
     * En caso de estar habilitado, el widget se mostrarÃ¡ siempre en disposiciÃ³n horizontal, rotÃ¡ndose 90 grados en disposiciones verticales.
     */
    @Input()
    public set forceLandscape(value: boolean) {
        this._widgetConfiguration.forceLandscape = value;
    }

    public get forceLandscape(): boolean { return this._widgetConfiguration.forceLandscape; }

    /**
     * Activa el modo de visualizaciÃ³n del drawer en pantallas de alta densidad.
     */
    @Input()
    public set canvasHD(value: boolean) {
        this._widgetConfiguration.canvasHD = value;
    }

    public get canvasHD(): boolean { return this._widgetConfiguration.canvasHD; }

    /**
     * Muestra un mensaje de advertencia de uso inicial tras la carga del widget.
     */
    @Input()
    public set initialTip(value: boolean) {
        this._widgetConfiguration.initialTip = value;
    }

    public get initialTip(): boolean { return this._widgetConfiguration.initialTip; }

    /**
     * ResoluciÃ³n horizontal preferida de la cÃ¡mara (el widget intentarÃ¡ seleccionar la resoluciÃ³n mÃ¡s cercana).
     */
    @Input()
    public set cameraWidth(value: number) {
        this._widgetConfiguration.cameraWidth = value;
    }

    public get cameraWidth(): number { return this._widgetConfiguration.cameraWidth; }

    /**
     * ResoluciÃ³n vertical preferida de la cÃ¡mara (el widget intentarÃ¡ seleccionar la resoluciÃ³n mÃ¡s cercana).
     */
    @Input()
    public set cameraHeight(value: number) {
        this._widgetConfiguration.cameraHeight = value;
    }

    public get cameraHeight(): number { return this._widgetConfiguration.cameraHeight; }

    /**
     * Identificador de la cÃ¡mara a utilizar para la realizaciÃ³n de la captura (si no se especifica, el widget seleccionarÃ¡ la cÃ¡mara trasera expuesta por defecto por el dispositivo).
     */
    @Input()
    public set cameraId(value: string) {
        this._widgetConfiguration.cameraId = value;
    }

    public get cameraId(): string { return this._widgetConfiguration.cameraId; }

    /**
     * Tiempo disponible para la captura de una imagen de documento antes de emitirse un timeout (por defecto 10s). 
     * Valores <= 0.0 anulan la limitaciÃ³n de tiempo de captura.
     */
    @Input()
    public set captureTimeout(value: number) {
        this._widgetConfiguration.captureTimeout = value;
    }

    public get captureTimeout(): number { return this._widgetConfiguration.captureTimeout; }

    /**
     * NÃºmero mÃ¡ximo de reintentos para obtener una captura vÃ¡lida de imagen de documento.
     * Valores <= 0 establecen un nÃºmero infinito de reintentos.
     */
    @Input()
    public set captureRetries(value: number) {
        this._widgetConfiguration.captureRetries = value;
    }

    public get captureRetries(): number { return this._widgetConfiguration.captureRetries; }

    
    /**
     * Sensibilidad de captura durante la detecciÃ³n de bordes del widget.
     */
    @Input()
    public set captureSensibility(value: number) {
        this._widgetConfiguration.captureSensibility = value;
    }

    public get captureSensibility(): number { return this._widgetConfiguration.captureSensibility; }

    /**
     * Formato de las imÃ¡genes capturadas por el widget (MIME type).
     */
    @Input()
    public set imageFormat(value: string) {
        this._widgetConfiguration.imageFormat = value;
    }

    public get imageFormat(): string { return this._widgetConfiguration.imageFormat; }

    /**
     * Factor de calidad de las imÃ¡genes devueltas (rango 0.0 a 1.0). Ãnicamente aplicable cuando el formato de imagen se ha especificado como "image/jpeg".
     */
    @Input()
    public set imageQuality(value: number) {
        this._widgetConfiguration.imageQuality = value;
    }

    public get imageQuality(): number { return this._widgetConfiguration.imageQuality; }

    /**
     * Activa el modo de depuraciÃ³n del widget, en el que se mostrarÃ¡n mensajes informativos en la consola del navegador.
     */
    @Input()
    public set debugMode(value: boolean) {
        this._widgetConfiguration.debugMode = value;
    }

    public get debugMode(): boolean { return this._widgetConfiguration.debugMode; }

    // #endregion

    // #region "Eventos"

    /**
     * Evento lanzado cuando los recursos del widget se han cargado correctamente.
     */
    @Output()
    public onComponentLoaded = new EventEmitter<any>();

    /**
     * Evento lanzado por el widget cuando se ha finalizado el proceso de carga e inicializaciÃ³n, tando del widget, como de los recursos asociados al mismo.
     */
    @Output()
    public onModuleLoaded = new EventEmitter<any>();

    /**
     * Evento lanzado cuando el proceso de extracciÃ³n ha finalizado correctamente. Devuelve el resultado de la captura.
     */
    @Output()
    public onExtractionFinished = new EventEmitter<any>();

    /**
     * Evento lanzado cuando el usuario cancela el proceso de extracciÃ³n.
     */
    @Output()
    public onUserCancelled = new EventEmitter<any>();

    /**
     * Evento lanzado en caso de que se produzca un error interno del widget.
     */
    @Output()
    public onExceptionCaptured = new EventEmitter<any>();

    /**
     * Evento lanzado si el proceso de captura excede el tiempo mÃ¡ximo disponible.
     */
    @Output()
    public onExtractionTimeout = new EventEmitter<any>();

    /**
     * Evento lanzado tras cualquier acciÃ³n realizada por el usuario sobre la interfaz grÃ¡fica del widget o ante cualquier cambio de estado
     * durante el proceso de captura.
     */
    @Output()
    public onTrackStatus = new EventEmitter<any>();

    // #endregion

    // #endregion

    // #region "Constructores"

    constructor(
        private widgetService: FPhiSelphIdWidgetService
    ) { }

    ngOnInit() {
        //console.warn(`>>>> [facephi-selphid] ngOnInit { autoLoadLibResources: ${this.autoLoadLibResources} }`, this.widgetContainer.nativeElement);
        //console.log(`widgetContainer.offsetWidth: ${(this.widgetContainer.nativeElement as any).offsetWidth}`);
        //console.log(`widgetContainer.offsetHeight: ${(this.widgetContainer.nativeElement as any).offsetHeight}`);
        this._widgetConfiguration.widgetContainer = this.widgetContainer.nativeElement;
        this.setupEvents();

        const widgetInitFunc = () => {
            //console.warn(">>>> [facephi-selphid]  CONTROL LOADED!!");
            this.onComponentLoaded.emit();

            this.widgetService
                .createUserControl(this._widgetConfiguration)
                .startUserControl();
        };

        if (this.autoLoadLibResources) {
            this.widgetService
                .loadControl(this.widgetLibPath)
                .subscribe(widgetInitFunc);
        } else
            widgetInitFunc();
    }

    ngOnDestroy() {
        if (this.autoLoadLibResources) {
            this.widgetService
                .stopUserControl()
                .removeControl();
        }
    }

    // #endregion

    // #region "MÃ©todos privados"

    private setupEvents(): FacePhiSelphIdComponent {
        this._widgetConfiguration.onModuleLoaded = eventData => { this.onModuleLoaded.emit(eventData); };
        this._widgetConfiguration.onExtractionFinished = extractionResult => { this.onExtractionFinished.emit(extractionResult); };
        this._widgetConfiguration.onUserCancelled = () => { this.onUserCancelled.emit(); };
        this._widgetConfiguration.onExceptionCaptured = exceptionResult => { this.onExceptionCaptured.emit(exceptionResult); };
        this._widgetConfiguration.onExtractionTimeout = () => { this.onExtractionTimeout.emit(); };
        this._widgetConfiguration.onTrackStatus = eventData => { this.onTrackStatus.emit(eventData); };

        return this;
    }

    // #endregion

}
