import { timer } from "rxjs/index";

export class RxjsHelper {

    /**
     * Encapsula una rutina de chequeo de una condiciÃ³n hasta que Ã©sta se cumpla.
     * @param pollTime Intervalo de chequeo (en ms).
     * @param evalFunc FunciÃ³n para evaluar la condiciÃ³n deseada.
     * @param successFunc FunciÃ³n a invocar cuando la condiciÃ³n se cumpla.
     * @param delayTime Tiempo de retardo para iniciar el proceso de chequeo. Por defecto 0ms.
     */
    public static poll(pollTime: number,
                       evalFunc: any,
                       successFunc: any,
                       delayTime: number = 0) {
        const subscription = timer(delayTime, pollTime)
            .subscribe(
                () => {
                    if (!evalFunc())
                        return;

                    subscription.unsubscribe();
                    successFunc();
                }
            );
    }
}
